@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');


body {
  position: relative;
  background-color: black;
  font-family: 'Oswald', sans-serif;
  background: url("wallpaper2.jpg");
  background-size: 100%;
  background-position-y: top;
  background-attachment: fixed;
  margin: 0px;
}

.App {
  text-align: none;
  overflow: hidden;
  user-select: none;
  height: 100%;
}

html {
  scroll-behavior: smooth;
  height: calc(100% + 80px);
  min-height: calc(100% + 80px);
}

.navbar {
  position: fixed;
  height: 10vmin;
  width: 100%;
  background-color: rgba(0, 0, 0, 6);
  z-index: 5;
  transition: 0.5s;
}

.navbar-items {
  width: 50%;
  height: 100%;
  display: flex;
  margin: auto;
  justify-content: space-between;
}

.navbar-item {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 3vmin;
  color: rgb(255, 255, 255);
}

.navbar-item:hover {
  color: rgb(182, 182, 182);
  cursor: pointer;
}

.wallpaper {
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
  transition: 0.5s;
}

.part {
  width: 20%;
  background: url("wallpaper1.jpg");
  background-size: 500% 100%;
}

.part1 {
  background-position-x: 25%;
  animation: 8s part1 forwards;
  animation-delay: 1s;
}

@keyframes part1 {
  0% {
    background-position-x: 25%;
  }
  100% {
    background-position-x: 0%;
  }
}

.part2 {
  background-position-x: 0%;
  animation: 8s part2 forwards;
  animation-delay: 1.2s;

}

@keyframes part2 {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 25%;
  }
}

.part3 {
  background-position-x: 0%;
  animation: 8s part3 forwards;
  animation-delay: 1.4s;

}

@keyframes part3 {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 50%;
  }
}

.part4 {
  background-position-x: 0%;
  animation: 8s part4 forwards;
  animation-delay: 1.6s;

}

@keyframes part4 {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 75%;
  }
}

.part5 {
  background-position-x: 0%;
  animation: 8s part5 forwards;
  animation-delay: 1.8s;

}

@keyframes part5 {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 100%;
  }
}

.scroll {
  display: block;
  position: relative;
  width: 8vmin;
  margin: auto;
  top: 85vh;
  filter: opacity(0.8);
  transition: 1s;
}

.scroll:hover {
  filter: opacity(1);
}

#container1 {
  position: relative;
  height: 100vh;
  width: 100%;
  perspective: 300px;
}

#container2 {
  height: auto;
  padding-bottom: 10vmin;
  background-color: rgba(245, 245, 245, 0.5);
}

#container3 {
  position: relative;
  height: 100vh;
  padding-bottom: 10vmin;
  background-size: cover;
  background-position-y: 70%;
  background-color: rgba(0, 0, 0, 0.26);
}

#container4 {
  position: relative;
  height: auto;

}

#container5 {
  height: 120vh;
}

.bottom-bar {
  height: 30vh;
  background-color: rgb(0, 0, 0);
  margin-top: -40px;
}


.text-wrapper {
  position: relative;
  top: 28vh;
  margin: auto;
  width: 40%;
  z-index: 2;
  transition: 0.6s ease-out;
  text-align: center;
}

@keyframes slideUp {
  0% {
    filter: opacity(0);
    top: 8vmin;
  }
  100% {
    filter: opacity(1);
    top: 0vmin;
  }
}

.large {
  position: relative;
  font-size: 6vmin;
  margin-top: 1vmin;
  margin-bottom: 1vmin;
  top: 8vmin;
  animation: 1s slideUp alternate forwards;
  animation-delay: 1s;
  color: rgb(0, 0, 0);
  filter: opacity(0);

}

.medium {
  position: relative;
  font-size: 1.3vmax;
  font-weight: 100;
  letter-spacing: 0.1vw;
  margin: 0;
  padding: 0;
  top: 8vmin;
  animation: 1s slideUp alternate forwards;
  animation-delay: 1s;
  filter: opacity(0);
}

.small {
  position: relative;
  font-weight: 400;
  font-size: 2.5vmin;
  letter-spacing: 0.1vmin;
  top: 8vmin;
  animation: 1s slideUp alternate forwards;
  animation-delay: 1s;
  filter: opacity(0);
}

.slide-header {
  margin: 0;
  font-size: 3.5vmin;
  font-weight: 400;
  margin-left: 2vmin;
  color: rgb(0, 0, 0);
}

.slide-footer {
  margin: 0;
  font-size: 2vmin;
  font-weight: 100;
  margin-left: 2vmin;
  color: rgb(0, 0, 0);
}

.more {
  transition: 0.2s;
  color: black;
}

.more:hover {
  color: rgb(255, 238, 0);
  transition: 0.2s;
  cursor: pointer;
}

.slide-down {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: max-content;
  font-size: 4vmin;
  font-weight: 500;
  color: white;
  top: 94%;
  animation: slideDown 2s infinite;
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

.slide-down:hover {
  color: rgb(99, 99, 99);
}


.header {
  position: relative;
  font-size: 6vmin;
  color: rgb(0, 0, 0);
  font-weight: 500;
  text-align: center;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 15vmin;
  margin-bottom: 10vmin;
}

.black {
  color: black;
}

.bottom-bar-text {
  color: white;
  position: relative;
  text-align: center;
  font-size: 1vmax;
  font-weight: 400;
  letter-spacing: 0.2vmin;
}

.project-wrapper {
  position: relative;
  width: 80%;
  margin: auto;
  padding: 3vmin;
  transition: 1s;
  overflow: hidden;
  filter: drop-shadow(0px 0px 10px rgb(182, 182, 182));

}

.project-hover {
  position: relative;
  transition: 0.5s;
  width: 47%;
  display: inline-block;
  margin-left: 1.5%;
  margin-right: 1.5%;
  top: 0px;
  filter:saturate(1);
  overflow: hidden;
}

.project {
  position: relative;
  width: 100%;
  height: 75%;
  background-color: rgba(0, 0, 0, 0.253);
  background-size: cover;
  outline: 0px;
  border: 0px;

}

.project-slide {
  position: relative;
  display: inline-block;
  height: 400px;
  width: 100%;
  margin: 1vw;
  background-color: rgba(255, 255, 255, 0.7);
  color: white;
}

.service-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 70%;
  left: 15%;
  margin-top: 5vh;
  padding: 3vmin;
  padding-bottom: 8vmin;
  padding-top: 8vmin;
  perspective: 600px;
}

.service {
  width: 30%;
  padding-top: 2%;
  padding-bottom: 2%;
  display: inline-block;
  color: white;
  transition: 0.4s;
  border-radius: 3vmin;
  background-color: rgba(0, 0, 0, 0.5);
}

.service:hover {
  background-color: black;
}

.service-title {
  position: relative;
  margin: auto;
  width: 75%;
}

.service-description {
  position: relative;
  text-align: center;
  font-size: 2.6vmin;
  width: 80%;
  margin: auto;
  margin-top: 3vw;
  background-repeat: no-repeat;
  background-position: top;
  filter: drop-shadow(0px 0px 50px rgb(255, 255, 255));
  transition: 0.5s;
  border-radius: 1vmin;
}

.skill-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:space-evenly;
  width: 60%;
  margin: auto;
  padding: 5vmin;
  border-radius: 3vmin;
  perspective: 600px;
}

.skill {
  position: relative;
  display: inline-grid;
  width: 10%;
  text-align: center;
  font-weight: 900;
  color: rgb(0, 0, 0);
  filter: drop-shadow(0px 0px 5px rgb(185, 185, 185));
  top: -0px;
  margin: 3vmin;
  padding: 1vmin;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 2vmin;
}

.tech-icon {
  width: 90%;
  display: block;
  margin: auto;
  border-radius: 0%;
}

.skill-description {
  position: relative;
  top: 0%;
  left: 5%;
  width: 90%;
  font-size: 1vmax;
  font-weight: 400;
  letter-spacing: 0;
  font-size: 1.8vmin;
  vertical-align: top;
}

.skill-title {
  background: linear-gradient(to top, rgb(0, 255, 191), rgb(0, 255, 106));
  height: fit-content;
  line-height: 5vmin;
  color: rgb(0, 0, 0);
}

.contact-wrapper {
  width: 50%;
  margin: auto;
  margin-top: 12vmin;
  padding: 5vmin;
  padding-top: 2vmin;
  padding-bottom: 2vmin;
  display: flex;
  border-radius: 3vmin;
}

.contact {
  display: block;
  margin: auto;
  margin-top: 3vmin;
  margin-bottom: 3vmin;
}

.contact-icon {
  width: 30%;
  display: block;
  margin: auto;
  border-radius: 0%;
  background-color: black;
  border-radius: 2vmin;
}


.navbar-icon {
  display: none;
}

@media only screen and (max-device-width: 1200px), (max-width: 1200px) {
  .skill {
    width: 18%;
  }
}

@media only screen and (max-device-width: 800px), (max-width: 800px) {
  .skill {
    width: 30%;
  }
}

@media only screen and (max-device-width: 600px), (max-width: 600px) {

.carousel-inner{
  height: 60vh;
}

  .carousel-caption{
    position: relative;
    width: 80%!important;
    top: 100px;
    left: 0;
    right: 0;
}

  body{
  background: none;
  background-attachment: scroll;
  }

  .text-wrapper {
    position: relative;
    display: block;
    width: 90%;
    z-index: 2;
    margin: auto;
    top: 50vmin;
    color: rgb(0, 0, 0);
  }

  .medium {
    font-size: 5vmin;
  }

  .large {
    font-size: 8vmin;
    width: 100%;
  }

  .header {
    font-size: 12vmin;
    padding-top: 10vmin;
  }

  .project-wrapper {
    width: 100%;
    padding: 0;
  }

  .project-slide {
    width: 100%;
    height: 275px;
  }

  .slide-header {
    font-size: 5vmin;
  }

  .slide-footer {
    font-size: 3.5vmin;
  }

  #container1 {
    left: 0%;
    height: 100vh;
  }

  #container2 {
    height: 100vh;
    padding-bottom: 50px;
    background: url("wallpaper2.jpg");
    background-size: cover;
  }

  #container3 {
    height: 100vh;
    background-size: cover;
    background: linear-gradient(rgb(255, 255, 255), rgb(189, 189, 189));
  }

  #container4 {
    background: linear-gradient(rgb(255, 255, 255), rgb(189, 189, 189));
    background: url("wallpaper2.jpg");
    background-size: cover;


  }

  .slide-down {
    top: 94%;
    z-index: 1;
    font-size: 7vmin;
  }

  .skill-wrapper {
    padding-left: 2vmin;
    padding-right: 2vmin;
    width: 90%;
  }

  .skill {
    width: 18%;
    font-size: 3.5vmin;
  }


  .skill-title {
    line-height: 4vmin;
    padding-top: 1vmin;
    padding-bottom: 1vmin;
  }

  .service-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-top: 0vh;
    padding-left: 0;
    padding-right: 0;
  }

  .service {
    width: 100%;
    margin-bottom: 12vmin;
    transform: rotateX(0deg);
  }

  .service:hover {
    transform: scale(1);
  }

  .service-title {
    position: relative;
    left: 15%;
    width: 70%;
    font-size: 7.5vmin;
    margin: 0;
    margin-top: 7vmin;
  }

  .service-description {
    width: 80%;
    font-size: 5vmin;
    padding-bottom: 2vmin;
  }


  .bottom-bar-text {
    font-size: 4vmin;
  }

  .small {
    font-size: 5vmin;
  }

  .project-hover {
    transition: 0.3s;
    width: 95%;
    display: inline-block;
    margin-left: 1.5%;
    margin-right: 1.5%;
    margin-bottom: 15vmin;

  }

  .project-hover:hover {
    transform: scale(1);
    transition: 0.3s;
  }

  .contact-wrapper {
    width: 80%;
    padding: 2vmin;
    flex-wrap: wrap;
    margin-top: 0;
  }

  .navbar {
    position: fixed;
    width: 100%;
    height: 10vh;
    z-index: 5;
    transition: 1s;
  }

  .navbar-items {
    width: 100%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    padding-left: 10%;
    padding-bottom: 40vh;
    padding-top: 10vh;
    justify-content: space-between;
    background-color: rgb(0, 0, 0);
    margin-left: 0%;
  }

  .navbar-item {
    position: relative;
    top: 0%;
    font-size: 8vmin;
    color: rgb(255, 255, 255);
  }

  .navbar-icon {
    position: absolute;
    height: 80%;
    top: 1vh;
    right: 5%;
    float: right;
    display: block;
  }

  .scroll {
    display: block;
    position: relative;
    width: 15vmin;
    margin: auto;
    padding-bottom: 10vmin;
    top: 40vh;
    filter: brightness(0);
  }

  .wallpaper{
    height: 100%;
  }

  .part {
    width: 20%;
    background: url("wallpaper1.jpg");
    background-size: 2000% 100%;
  }

  .part1{
  background-position-x: 25%;
  }

  .tech-icon {
    width: 80%;
    display: block;
    margin: auto;
    border-radius: 0%;
  }

  .contact {
    width: 15%;
    margin-top: 0vmin;
    margin-bottom: 0vmin;
  }

  .contact-icon {
    width: 70%;
    display: block;
    margin: auto;
    border-radius: 0%;
    background-color: black;
    padding: 2vmin;
    border-radius: 2vmin;
  }

  .bottom-bar {
    height: 35vh;
    background-color: rgb(0, 0, 0);
  }

}
